import React from "react";
import { Link } from "@reach/router";

const NavLink = (props) => (
  <Link
    {...props}
    getProps={({ isCurrent }) => {
      // the object returned here is passed to the
      // anchor element's props
      return {
        className: isCurrent ? "active" : "non-active",
      };
    }}
  />
);

const FeatureHome = ({ data }) => {
  return (
    <div
      style={{ background: "rgb(217, 217, 217)" }}
      className="row d-flex justify-content-center align-items-center"
    >
      <div className="col-lg-6 col-12 ">
        <h6
          style={{
            color: "rgb(30, 30, 30)",
            textTransform: "uppercase",
            letterSpacing: "1px",
            marginBottom: "40px",
          }}
        >
          FEATURED
        </h6>
        <h1
          class="d-flex justify-content-start justify-content-sm-start"
          style={{
            color: "rgb(30, 30, 30)",
            marginBottom: "40px",
            justifyContent: "flex-start",
          }}
        >
          (ID<div style={{ color: "rgb(128, 128, 128)" }}>X</div>)
        </h1>
        <h4
          style={{
            color: "rgb(30, 30, 30)",
            fontWeight: "800",
            fontSize: "22px",
            marginBottom: "40px",
          }}
        >
          {data.intro_section_title}
        </h4>
        <p
          class=""
          style={{
            color: "rgb(30, 30, 30)",
            fontSize: "15px",
            marginBottom: "40px",
          }}
        >
          {data.intro_section_text}
        </p>
        <span
          onClick={() => window.open("/wallet", "_self")}
          className="btn-main btn-main-custom1 lead"
          style={{
            borderRadius: "6px",
            marginRight: "10px",
            backgroundColor: "black",
            color: "#FF343F",
          }}
        >
          {data.intro_section_button_text}
        </span>
      </div>
      <div className="col-lg-6 col-12 m-lg-0 mt-1">
        <video
          src="./img/side_video.mp4"
          muted
          typeof="video/mp4"
          autoPlay
          width="100%"
          controls
        ></video>
      </div>
    </div>
  );
};

export default FeatureHome;
