import React from 'react';
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import axios from 'axios';
import { useEffect, useState } from 'react';
import "../../GlobalVariables"
import Faq from '../components/Faq';

const GlobalStyles = createGlobalStyle`
  .navbar {
    border-bottom: solid 1px rgba(255, 255, 255, .1) !important;
  }
`;

const FAQ = () => {

    useEffect(() => {
        axios.get(`${global.BackendUrl}/getFaqs`).then((response) => {
            setFaqs(response.data)
        }).catch((err) => {
            console.log(err)
        })
        axios.get(`${global.BackendUrl}/getHeroBg`).then((response) => {
            setHeroBg(response.data)
        }).catch((err) => {
            console.log(err)
        })
    }, [])

    const [HeroBg, setHeroBg] = useState([])
    const [Faqs, setFaqs] = useState([])

    return (
        <div>
            <GlobalStyles />
            <section className='jumbotron breadcumb no-bg' style={{ backgroundImage: `url(https://idxadminpanel.unialsolutions.com/assets/front/img/sliders/${HeroBg.map((img) => { return img.image })})` }}>
                <div className='mainbreadcumb'>
                    <div className='container'>
                        <div className='row m-10-hor'>
                            <div className='col-12 text-center'>
                                <h1>FAQ's</h1>
                                <div className="spacer-20"></div>
                                <p className="mt-0">All Your Answers Are Here</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section className='container'>
                <div className="row">
                    <div className="col">
                    </div>
                </div>
                <div className="row">
                    {Faqs.map((data) => {
                        return <Faq question={data.question} answer={data.answer} />
                    })}
                </div>
            </section>

            <Footer />
        </div>
    )
};
export default FAQ;