import React from "react";
import Footer from "../components/footer";
import { createGlobalStyle } from "styled-components";
import HelpCenter from "../components/HelpCenter";
import axios from "axios";
import { useEffect, useState } from "react";
import "../../GlobalVariables";

const GlobalStyles = createGlobalStyle`
  .navbar {
    border-bottom: solid 1px rgba(255, 255, 255, .1) !important;
  }
`;

const Logintwo = () => {
  useEffect(() => {
    axios
      .get(`${global.BackendUrl}/getArticleCategories`)
      .then((response) => {
        setCArticle(response.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const [CArticle, setCArticle] = useState([]);

  return (
    <div>
      <GlobalStyles />

      <section className="jumbotron breadcumb no-bg">
        <div className="mainbreadcumb">
          <div className="container">
            <div className="row">
              <div className="col-md-8 offset-md-2 text-center">
                <h1>Help Center</h1>
                <div className="spacer-20"></div>
                <form className="row" id="form_sb" name="myForm">
                  <div className="col text-center">
                    <input
                      className="form-control"
                      id="name_1"
                      name="name_1"
                      placeholder="type your question here"
                      type="text"
                    />{" "}
                    <button id="btn-submit">
                      <i className="arrow_right"></i>
                    </button>
                  </div>
                </form>
                <div className="spacer-20"></div>

                <p className="mt-0">eg. create item, create wallet.</p>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="container">
        <div className="row">
          <div className="col"></div>
        </div>
        <div className="row">
          {CArticle.map((data) => {
            return <HelpCenter key={data.id} name={data.name} />;
          })}
        </div>
      </section>

      <Footer />
    </div>
  );
};
export default Logintwo;
