import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const Faq = ({ question, answer }) => {

    return (
        <div className="col-12 mb-4">
            <Accordion style={{ background: "#212428", color: "#fff", boxShadow: "0 4px 20px 0 rgb(10 10 10 / 80%)" }}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon style={{ color: "#3bffae" }} />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography style={{ fontFamily: "DM Sans", fontWeight: "300" }}>{question}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography style={{ fontFamily: "DM Sans", fontWeight: "300" }}>
                        {answer}
                    </Typography>
                </AccordionDetails>
            </Accordion>
        </div>
    )
}

export default Faq;