import React, { Component, useEffect } from "react";
import Clock from "./Clock";
import axios from "axios";
import { Link } from "@reach/router";
import "../../assets/CSSfiles/InvestCarousel.scss";

const NavLink = (props) => (
  <Link
    {...props}
    getProps={({ isCurrent }) => {
      return {
        className: isCurrent ? "active" : "non-active",
      };
    }}
  />
);

const ColumnNew = (props) => {
  useEffect((props) => {
    console.log(props);
  }, []);
  function makeid(length) {
    var result = '';
    var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() *
        charactersLength));
    }
    return result;
  }
  function likeNFT(nftitem_id, user_nft_id) {
    let walletAddress = window.localStorage.getItem("walletAddress");
    console.log(walletAddress)
    if (walletAddress != undefined && walletAddress != null && walletAddress != "") {
      console.log(nftitem_id)
      console.log(user_nft_id)
      // if(liked==false)
      // {
      const formData = {
        user_nft_id: user_nft_id,
        nftitem_id: nftitem_id,
      }
      axios.post(`${global.Laravel_API_URL}/api/mint-tokens/like-dislike-nft`, formData).then(response => {
        console.log(response)
        if (response.data.status == 200) {
          this.getTokens()
        }
      })
      // }
    }
    else {

    }
  }
  return (
    <div className="col-12 col-sm-6 col-lg-3 col-md-4 col-xl-3 col-xxl-3 d-flex justify-content-center my-2">
      <div className="carousel_item">
        <div className="author">
          <NavLink to={"/Author/user/" + makeid(5) + props.data.user_id}>
            <img
              className="author_img hoverZoomLink"
              src={
                props.user.userData.length > 0
                  ? `${global.BackendUrl}/nodeassets/${props.data.user_image}`
                  : ""
              }
              alt=""
            />
          </NavLink>
        </div>
        <div className="nft_img_wrap" style={{ height: "240px" }}>
          <img
            src={global.BackendUrl + "/nodeassets/" + props.data.nftitem_media}
            className="lazy nft_img"
            alt=""
          />
        </div>
        <div className="nft_item_info">
          <NavLink to={"/ItemDetail/" + makeid(5) + props.data.nftitem_id}>
            <span>
              <h4 className="nft_heading">{props.data.nftitem_title}</h4>
            </span>
          </NavLink>
          <div>
            {props.data.nftitem_shares_total} /{props.data.nftitem_royalities}
          </div>

          <div className="idx_like">
            <div onClick={() => window.open(`${props.data.nftLink}`, "_self")}>
              <span style={{ color: "white" }}>IDX</span>
            </div>

            <div
              onClick={(e) => {
                if (props.currentUser.userData.length > 0) {
                  likeNFT(
                    props.data.nftitem_id,
                    props.currentUser.userData[0].user_id
                  );
                }
              }}
              className={`nft__item_like ${props.data.liked == true ? "nft__item_like_active" : ""
                }`}
            >
              <i className="fa fa-heart"></i>
              <span>{props.data.likes}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default ColumnNew;
