import React from 'react';
import { Link } from '@reach/router';
import { useState, useEffect } from 'react';
import axios from 'axios';

const Footer = () => {

    useEffect(() => {
        axios.get(`${global.BackendUrl}/BasicInfo`).then((response) => {
            setFooterData(response.data)
        }).catch((err) => {
            console.log(err)
        })
        axios.get(`${global.BackendUrl}/getSocials`).then((response) => {
            setSocialLinks(response.data)
        }).catch((err) => {
            console.log(err)
        })
    }, [])

    const [FooterData, setFooterData] = useState([])
    const [SocialLinks, setSocialLinks] = useState([])

    const [email, setemail] = useState("")

    const handleSubscribe = (e) => {
        e.preventDefault();
        if (email == "") {
            console.log("please enter email")
        }
        else {
            axios.post(`${global.BackendUrl}/subscribers`, {
                email
            }).then(() => {
                console.log("Submitted")
            }).catch((err) => {
                console.log(err)
            })
        }
    }

    return (
        FooterData.map((data) => {
            return (
                <footer className="footer-light" style={{ backgroundColor: "#171719" }}>
                    <div className="container">
                        <div className="row">
                            <div className="col-md-3 col-sm-6 col-xs-1">
                                <div className="widget">
                                    <h5 style={{ color: "#3bffae", letterSpacing: "3px" }}>Marketplace</h5>
                                    {/* <ul>
                                        <li><Link to="">All NFTs</Link></li>
                                        <li><Link to="">Art</Link></li>
                                        <li><Link to="">Music</Link></li>
                                        <li><Link to="">Domain Names</Link></li>
                                        <li><Link to="">Virtual World</Link></li>
                                        <li><Link to="">Collectibles</Link></li>
                                    </ul> */}
                                </div>
                            </div>
                            <div className="col-md-3 col-sm-6 col-xs-1">
                                <div className="widget">
                                    <h5 style={{ color: "#3bffae", letterSpacing: "3px" }}>Resources</h5>
                                    <ul>
                                        <li><Link to="/helpcenter">Help Center</Link></li>
                                        <li><Link to="/faq">Docs</Link></li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-md-3 col-sm-6 col-xs-1">
                                <div className="widget">
                                    <h5 style={{ color: "#3bffae", letterSpacing: "3px" }}>Community</h5>
                                    <ul>
                                        <li><Link to="/news">Community</Link></li>
                                        <li><Link to="/faq">Documentation</Link></li>
                                        <li><Link to="/news">Blog</Link></li>
                                        <li><Link to="/faq">Faq</Link></li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-md-3 col-sm-6 col-xs-1">
                                <div className="widget">
                                    <h5 style={{ color: "#3bffae", letterSpacing: "3px" }}>Newsletter</h5>
                                    <p>{data.newsletter_text}</p>
                                    <form action="#" className="row form-dark" id="form_subscribe" method="post" name="form_subscribe">
                                        <div className="col text-center">
                                            <input className="form-control" id="txt_subscribe" name="email" placeholder="enter your email" type="text" onChange={(e) => setemail(e.target.value)} />
                                            <div id="btn-subscribe" onClick={handleSubscribe} style={{ cursor: "pointer" }}>
                                                <i className="arrow_right bg-color-secondary"></i>
                                            </div>
                                            <div className="clearfix"></div>
                                        </div>
                                    </form>
                                    <div className="spacer-10"></div>
                                    <small>Your email is safe with us. We don't spam.</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='subfooter' style={{ backgroundColor: "#1e1e1e" }}>
                        <div className="container">
                            <div className="row d-flex justify-content-between align-items-center">
                                <div className="col-lg-3 col-12 text-lg-start text-center">
                                    <img
                                        src={`https://idxadminpanel.unialsolutions.com/assets/front/img/${data.footer_logo}`}
                                        className=" d-block"
                                        alt="#"
                                        style={{ width: "170px" }}
                                    />
                                </div>
                                <div className="col-lg-6 col-12 text-lg-start text-center">
                                    <div style={{ letterSpacing: "7px", display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center", textAlign: "center", marginTop: "0px", fontSize: "12px", color: "white", fontFamily: "sans-serif" }} class="" bis_skin_checked="1">DREAM   ALWAYS
                                        <div style={{ color: "rgb(59, 255, 174)" }} className="ms-2 me-2" bis_skin_checked="1">|</div>
                                        IDEAL  EXCHANGE
                                    </div>
                                </div>
                                <div className="col-lg-3 col-12 text-lg-start text-center">
                                    <div className="social-icons">
                                        {SocialLinks.map((data) => {
                                            return (
                                                <a href={data.url} target="_blank" style={{ margin: "0px 10px", fontSize: "1.3em" }}><i className={data.icon}></i></a>
                                            )
                                        })}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* <div className="subfooter">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="de-flex">
                                        <div className="de-flex-col">
                                            <span>
                                                <img
                                                    src={`https://idxadminpanel.unialsolutions.com/assets/front/img/${data.footer_logo}`}
                                                    className=" d-block"
                                                    alt="#"
                                                    style={{ width: "170px" }}
                                                />
                                            </span>
                                            <div style={{ letterSpacing: "5px", display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center", textAlign: "center", marginTop: "20px" }} class="MuiBox-root jss17 jss63" bis_skin_checked="1">DREAM   ALWAYS
                                                <div style={{ color: "rgb(59, 255, 174)" }} className="ms-2 me-2" bis_skin_checked="1">|</div>
                                                IDEAL  EXCHANGE
                                            </div>
                                        </div>
                                        <div className="de-flex-col">
                                            <div className="social-icons">
                                                {SocialLinks.map((data) => {
                                                    return (
                                                        <a href={data.url} style={{ margin: "0px 10px", fontSize: "1.3em" }}><i className={data.icon}></i></a>
                                                    )
                                                })}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div> */}
                </footer >
            )
        })
    )
};
export default Footer;