import React, { useEffect, useState } from "react";
import Breakpoint, {
  BreakpointProvider,
  setDefaultBreakpoints,
} from "react-socks";
import { header } from "react-bootstrap";
import { Link } from "@reach/router";
import Scroller from "../components/Scroller";
import useOnclickOutside from "react-cool-onclickoutside";
// import { NavLink } from "react-router-dom"
import { useSelector, useDispatch } from "react-redux";
import { setAddress, setWalletStatus } from "../../store/WalletSlice";
import { setUserData } from "../../store/UserSlice";
import { navigate } from "@reach/router";
import axios from "axios";

setDefaultBreakpoints([{ xs: 0 }, { l: 1199 }, { xl: 1200 }]);

const NavLink = (props) => (
  <Link
    {...props}
    getProps={({ isCurrent }) => {
      // the object returned here is passed to the
      // anchor element's props
      return {
        className: isCurrent ? "active" : "non-active",
      };
    }}
  />
);

const Header = function () {
  const dispatch = useDispatch();

  const wallet = useSelector((state) => state.wallet);
  const user = useSelector((state) => state.user);

  const [openMenu, setOpenMenu] = React.useState(false);
  const [openMenu1, setOpenMenu1] = React.useState(false);
  const [openMenu2, setOpenMenu2] = React.useState(false);
  const [openMenu3, setOpenMenu3] = React.useState(false);

  const [notificationList, setNotificationList] = useState([]);
  useEffect(() => {
    getNotifications();
  }, []);
  function getNotifications() {
    if (user.userData.length > 0) {
      console.log(user.userData[0].user_id);
      const formDataNotification = {
        user_id: user.userData[0].user_id,
      };
      axios
        .post(
          `${global.Laravel_API_URL}/api/mint-tokens/get-notification`,
          formDataNotification
        )
        .then((response) => {
          console.log(response);
          setNotificationList(response.data.notifications);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }

  const handleBtnClick = (): void => {
    setOpenMenu(!openMenu);
  };
  const handleBtnClick1 = (): void => {
    setOpenMenu1(!openMenu1);
  };
  const handleBtnClick2 = (): void => {
    setOpenMenu2(!openMenu2);
  };
  const handleBtnClick3 = (): void => {
    setOpenMenu3(!openMenu3);
  };
  const closeMenu = (): void => {
    setOpenMenu(false);
  };
  const closeMenu1 = (): void => {
    setOpenMenu1(false);
  };
  const closeMenu2 = (): void => {
    setOpenMenu2(false);
  };
  const closeMenu3 = (): void => {
    setOpenMenu3(false);
  };
  const ref = useOnclickOutside(() => {
    closeMenu();
  });
  const ref1 = useOnclickOutside(() => {
    closeMenu1();
  });
  const ref2 = useOnclickOutside(() => {
    closeMenu2();
  });
  const ref3 = useOnclickOutside(() => {
    closeMenu3();
  });

  const [showmenu, btn_icon] = useState(false);
  useEffect(() => {
    const header = document.getElementById("myHeader");
    const totop = document.getElementById("scroll-to-top");
    const sticky = header.offsetTop;
    const scrollCallBack = window.addEventListener("scroll", () => {
      btn_icon(false);
      if (window.pageYOffset > sticky) {
        header.classList.add("sticky");
        totop.classList.add("show");
      } else {
        header.classList.remove("sticky");
        totop.classList.remove("show");
      }
      if (window.pageYOffset > sticky) {
        closeMenu();
      }
    });
    return () => {
      window.removeEventListener("scroll", scrollCallBack);
    };
  }, []);

  const [MultiDropDown, setMultiDropDown] = useState(false);
  const [LogoutBtn, setLogoutBtn] = useState(false);
  const [NotificationPopup, setNotificationPopup] = useState(false);

  const handleWalletLogout = () => {
    dispatch(setWalletStatus(0));
    dispatch(setAddress(""));
    dispatch(setUserData([]));
    navigate("/home1");
  };

  return (
    <div>
      <header
        id="myHeader"
        className="navbar white"
        style={{ background: "rgb(30, 30, 30)" }}
      >
        <div className="container">
          <div className="row w-100-nav">
            <div className="logo px-0">
              <div className="navbar-title navbar-item">
                <NavLink to="/home1">
                  <img
                    src="./img/logo.png"
                    className=" d-block"
                    alt="#"
                    style={{ width: "170px" }}
                  />
                  <img
                    src="./img/logo.png"
                    className=" d-3"
                    alt="#"
                    style={{ width: "auto", height: "36px" }}
                  />
                  <img
                    src="./img/logo.png"
                    className=" d-none"
                    alt="#"
                    style={{ width: "170px" }}
                  />
                </NavLink>
              </div>
            </div>

            <div className="search">
              <input
                class="xs-hide"
                id="quick_search"
                aria-autocomplete="list"
                autocomplete="off"
                autocapitalize="none"
                spellcheck="false"
                name="quick_search"
                placeholder="Search artworks, creators and collectors..."
                type="text"
                style={{ fontFamily: "inherit" }}
              />
            </div>

            <BreakpointProvider>
              <Breakpoint
                l
                down
                style={{
                  position: "absolute",
                  backgroundColor: "rgb(30, 30, 30)",
                  top: "75px",
                  padding: "0",
                  left: "0",
                }}
              >
                {showmenu && (
                  <div className="container">
                    <div className="navbar-item">
                      <NavLink
                        to="/home1"
                        className="dropdown-custom dropdown-toggle btn"
                        onClick={handleBtnClick}
                      >
                        IDEAL EXCHANGE
                      </NavLink>
                    </div>
                    <div className="navbar-item">
                      <NavLink
                        to="/Originsclub"
                        className="dropdown-custom dropdown-toggle btn"
                        onClick={handleBtnClick1}
                      >
                        ORIGINS CLUB
                      </NavLink>
                    </div>
                    <div className="navbar-item">
                      <div ref={ref2}>
                        <div
                          className="dropdown-custom dropdown-toggle btn"
                          onClick={handleBtnClick2}
                        >
                          CATALOG
                        </div>
                        {openMenu2 && (
                          <div className="item-dropdown">
                            <div className="dropdown" onClick={closeMenu2}>
                              <NavLink
                                to="/explore"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Invest
                              </NavLink>
                              <NavLink
                                to="/home1"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Pro: Tier A
                              </NavLink>
                              <NavLink
                                to="/home1"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Advanced: Tier B
                              </NavLink>
                              <NavLink
                                to="/home1"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Beginner: Tier C
                              </NavLink>
                              <NavLink
                                to="/home1#popular"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Popular Ideas
                              </NavLink>
                              <NavLink
                                to="/home1#topProject"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Top Projects
                              </NavLink>
                              <NavLink
                                to="/headerActivity"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Activity
                              </NavLink>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="navbar-item">
                      <div ref={ref1}>
                        <div
                          className="dropdown-custom dropdown-toggle btn"
                          onClick={handleBtnClick1}
                        >
                          MY PORTFOLIO
                        </div>
                        {openMenu1 && (
                          <div className="item-dropdown">
                            <div className="dropdown" onClick={closeMenu1}>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/create2"
                                    : "/wallet"
                                }
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Create
                              </NavLink>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/Author#IP"
                                    : "/wallet"
                                }
                                onClick={() => btn_icon(!showmenu)}
                              >
                                IP
                              </NavLink>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/Author#dreamInvestment"
                                    : "/wallet"
                                }
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Dream Investments
                              </NavLink>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/Author#sharedDreams"
                                    : "/wallet"
                                }
                                onClick={() => btn_icon(!showmenu)}
                              >
                                My Shared Dreams
                              </NavLink>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="navbar-item">
                      <div ref={ref3}>
                        <div
                          className="dropdown-custom dropdown-toggle btn"
                          onClick={handleBtnClick3}
                        >
                          COMMUNITY
                        </div>
                        {openMenu3 && (
                          <div className="item-dropdown">
                            <div className="dropdown" onClick={closeMenu3}>
                              <NavLink
                                to="/news"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Blogs
                              </NavLink>
                              <NavLink
                                to="/helpcenter"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Help Center
                              </NavLink>
                              <NavLink
                                to="/NewsLetter"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                Newsletter
                              </NavLink>
                              <NavLink
                                to="/faq"
                                onClick={() => btn_icon(!showmenu)}
                              >
                                FAQ's
                              </NavLink>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                )}
              </Breakpoint>

              <Breakpoint xl>
                <div className="menu d-flex justify-content-center align-items-center">
                  <img
                    src="./img/favicon.png"
                    alt="faviconImage"
                    width="auto"
                    height="26px"
                    style={{ marginRight: "1px" }}
                  />
                  <div className="navbar-item">
                    <NavLink to="/home1">
                      IDEAL EXCHANGE
                      <span className="lines"></span>
                    </NavLink>
                  </div>
                  <div className="navbar-item">
                    <NavLink to="/Originsclub">
                      ORIGINS CLUB
                      <span className="lines"></span>
                    </NavLink>
                  </div>
                  <div className="navbar-item">
                    <div ref={ref2}>
                      <div
                        className="dropdown-custom btn"
                        onMouseEnter={handleBtnClick2}
                        onMouseLeave={closeMenu2}
                      >
                        CATALOG
                        <span className="lines"></span>
                        {openMenu2 && (
                          <div className="item-dropdown">
                            <div className="dropdown" onClick={closeMenu2}>
                              <span style={{ position: "relative" }}>
                                <NavLink
                                  to="/explore"
                                  onMouseEnter={() =>
                                    setMultiDropDown(!MultiDropDown)
                                  }
                                >
                                  Invest
                                </NavLink>
                                <div
                                  className={`item-dropdown ${
                                    MultiDropDown ? "d-block" : "d-none"
                                  }`}
                                  style={{
                                    position: "absolute",
                                    top: "0",
                                    left: "105px",
                                  }}
                                >
                                  <div className="dropdown">
                                    <NavLink to="/home1">Pro: Tier A</NavLink>
                                    <NavLink to="/home1">
                                      Advanced: Tier B
                                    </NavLink>
                                    <NavLink to="/home1">
                                      Beginner: Tier C
                                    </NavLink>
                                  </div>
                                </div>
                              </span>
                              <NavLink
                                to="/home1#popular"
                                onMouseEnter={() => setMultiDropDown(false)}
                              >
                                Popular Ideas
                              </NavLink>
                              <NavLink
                                to="/home1#topProject"
                                onMouseEnter={() => setMultiDropDown(false)}
                              >
                                Top Projects
                              </NavLink>
                              <NavLink
                                to="/headerActivity"
                                onMouseEnter={() => setMultiDropDown(false)}
                              >
                                Activity
                              </NavLink>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="navbar-item">
                    <div ref={ref1}>
                      <div
                        className="dropdown-custom  btn"
                        onMouseEnter={handleBtnClick1}
                        onMouseLeave={closeMenu1}
                      >
                        MY PORTFOLIO
                        <span className="lines"></span>
                        {openMenu1 && (
                          <div className="item-dropdown">
                            <div className="dropdown" onClick={closeMenu1}>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/create2"
                                    : "/wallet"
                                }
                              >
                                Create
                              </NavLink>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/Author#IP"
                                    : "/wallet"
                                }
                              >
                                IP
                              </NavLink>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/Author#dreamInvestment"
                                    : "/wallet"
                                }
                              >
                                Dream Investments
                              </NavLink>
                              <NavLink
                                to={
                                  wallet.walletStatus == 1
                                    ? "/Author#sharedDreams"
                                    : "/wallet"
                                }
                              >
                                My Shared Dreams
                              </NavLink>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="navbar-item">
                    <div ref={ref3}>
                      <div
                        className="dropdown-custom btn"
                        onMouseEnter={(e) => {
                          handleBtnClick3();
                          setNotificationPopup(false);
                        }}
                        onMouseLeave={closeMenu3}
                      >
                        COMMUNITY
                        <span className="lines"></span>
                        {openMenu3 && (
                          <div className="item-dropdown">
                            <div className="dropdown" onClick={closeMenu3}>
                              <NavLink to="/news">Blogs</NavLink>
                              <NavLink to="/helpcenter">Help Center</NavLink>
                              <NavLink to="/NewsLetter">Newsletter</NavLink>
                              <NavLink to="/faq">FAQ's</NavLink>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                  {wallet.walletStatus == 1 ? (
                    <div
                      className="navbar-item"
                      style={{ position: "relative" }}
                    >
                      <i
                        className="fa-solid fa-bell"
                        style={{
                          color: "rgb(187, 187, 187)",
                          fontSize: "25px",
                          cursor: "pointer",
                        }}
                        onMouseEnter={() => {
                          setNotificationPopup(!NotificationPopup);
                          setLogoutBtn(false);
                        }}
                      ></i>
                      <div
                        className={`headerNotificationPopup ${
                          NotificationPopup ? "d-block" : "d-none"
                        }`}
                        onMouseLeave={() =>
                          setNotificationPopup(!NotificationPopup)
                        }
                      >
                        {notificationList.map((item, index) => (
                          <p
                            onClick={(e) => {
                              navigate("/headerActivity");
                            }}
                            style={{ marginBottom: "5px", cursor: "pointer" }}
                          >
                            {item.notification_message}
                          </p>
                        ))}
                      </div>
                    </div>
                  ) : (
                    ""
                  )}
                </div>
              </Breakpoint>
            </BreakpointProvider>

            <div className="mainside">
              {wallet.walletStatus == 0 ? (
                <NavLink to="/wallet" className="btn-main text-truncate">
                  Connect Wallet
                </NavLink>
              ) : (
                <div style={{ position: "relative" }}>
                  <img
                    className="avatarImageHome"
                    src={
                      user.userData.length > 0
                        ? `${global.BackendUrl}/nodeassets/${user.userData[0].user_image}`
                        : ""
                    }
                    alt="ProfileImg"
                    width={50}
                    style={{ borderRadius: "50%", cursor: "pointer" }}
                    onMouseEnter={() => {
                      setLogoutBtn(!LogoutBtn);
                      setNotificationPopup(false);
                    }}
                  />
                  <div
                    className={`flex-column ${LogoutBtn ? "d-flex" : "d-none"}`}
                    style={{
                      borderRadius: "6px",
                      position: "absolute",
                      top: "7px",
                      marginLeft: "-90px",
                    }}
                  >
                    <div
                      className="item-dropdown"
                      onMouseLeave={() => setLogoutBtn(!LogoutBtn)}
                    >
                      <div
                        className="dropdown d-flex flex-column"
                        style={{
                          padding: "0px 12px",
                          background: "#1e1e1e",
                          borderRadius: "10px",
                          overflow: "hidden",
                        }}
                      >
                        <div
                          className="row d-flex walletDropdownStyle"
                          style={{ padding: "5px 0px" }}
                          onClick={() => window.open("/Author", "_self")}
                        >
                          <div className="col-3">
                            <i
                              className="fa-solid fa-circle-user"
                              style={{ color: "white" }}
                            ></i>
                          </div>
                          <div className="col-9">
                            <span
                              style={{ borderRadius: "6px", color: "#fff" }}
                            >
                              My Profile
                            </span>
                          </div>
                        </div>
                        <hr style={{ margin: "5px 0px" }} />
                        <div
                          className="row d-flex mt-1 walletDropdownStyle"
                          style={{ padding: "5px 0px" }}
                          onClick={handleWalletLogout}
                        >
                          <div className="col-3">
                            <i
                              className="fa-solid fa-arrow-right-from-bracket"
                              style={{ color: "white" }}
                            ></i>
                          </div>
                          <div className="col-9">
                            <span
                              style={{ borderRadius: "6px", color: "#fff" }}
                            >
                              Logout
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>

          <button className="nav-icon" onClick={() => btn_icon(!showmenu)}>
            <div className="menu-line white"></div>
            <div className="menu-line1 white"></div>
            <div className="menu-line2 white"></div>
          </button>
        </div>
      </header>
    </div>
  );
};
export default Header;
