import React, { useState, useEffect } from "react";
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import { useParams } from "@reach/router";
import "../../GlobalVariables"
import axios from 'axios';

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.sticky.white {
    background: #403f83;
    border-bottom: solid 1px #403f83;
  }
  header#myHeader.navbar .search #quick_search{
    color: #fff;
    background: rgba(255, 255, 255, .1);
  }
  header#myHeader.navbar.white .btn, .navbar.white a, .navbar.sticky.white a{
    color: #fff;
  }
  header#myHeader .dropdown-toggle::after{
    color: rgba(255, 255, 255, .5);
  }
  header#myHeader .logo .d-block{
    display: none !important;
  }
  header#myHeader .logo .d-none{
    display: block !important;
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #fff;
    }
    .item-dropdown .dropdown a{
      color: #fff !important;
    }
  }
`;

const NewsSingle = () => {

    const { id } = useParams();

    useEffect(() => {
        axios.get(`${global.BackendUrl}/getBlogs`).then((response) => {
            setBlogsData(response.data)
        }).catch((err) => {
            console.log(err)
        })
    }, [])

    const [BlogsData, setBlogsData] = useState([])

    const SingleBlogData = BlogsData.filter((data) => {
        return data.id == id
    })

    return (
        <div className="greyscheme">
            <GlobalStyles />

            {SingleBlogData.map((data) => {
                return (
                    <section className='jumbotron breadcumb no-bg' style={{ backgroundImage: `url(${'../img/background/subheader.jpg'})` }}>
                        <div className='mainbreadcumb'>
                            <div className='container'>
                                <div className='row m-10-hor'>
                                    <div className='col-12 text-center'>
                                        <h1>{data.title}</h1>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                )
            })}
            {SingleBlogData.map((data) => {
                return (
                    <section aria-label="section">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-8">
                                    <div className="blog-read">
                                        <img alt="" src={`https://idxadminpanel.unialsolutions.com/assets/front/img/blogs/${data.main_image}`} className="img-fullwidth rounded" width="100%" />

                                        <div className="post-text">
                                            <p>Sunt duis laboris ex et quis laborum laborum cillum mollit voluptate culpa consequat ex cupidatat dolor eiusmod proident proident cillum pariatur sint adipisicing in nostrud do dolore consectetur quis incididunt minim consectetur. Exercitation elit proident dolor est id eiusmod dolor dolor incididunt ad voluptate laboris cupidatat est est sint veniam sint officia sint incididunt est sit ut tempor commodo pariatur ut proident et do.

                                                Sed eu in ut sint dolor irure fugiat minim veniam sed ea proident ullamco occaecat irure ut velit eu ullamco fugiat cupidatat dolore fugiat. Lorem ipsum id non deserunt id consequat duis voluptate amet aliqua pariatur laboris officia pariatur veniam velit reprehenderit sint nostrud cupidatat magna eiusmod mollit exercitation pariatur nulla minim laboris dolore aliqua consectetur cillum duis aute consectetur.</p>
                                            <span className="post-date">April 1, 2018</span>

                                        </div>

                                    </div>
                                </div>

                                <div id="sidebar" className="col-md-4">
                                    {/* <div className="widget widget-post">
                                <h4>Recent Posts</h4>
                                <div className="small-border"></div>
                                <ul>
                                    <li><span className="date">date</span><a href="/news">title</a></li>
                                </ul>
                            </div>

                            <div className="widget widget-text">
                                <h4>About Us</h4>
                                <div className="small-border"></div>
                                author
                            </div> */}
                                    <div className="widget widget_tags">
                                        <h4>Tags</h4>
                                        <ul>

                                            <li><a href="#link" style={{ color: "#3bffae" }}>{data.meta_keywords}</a></li>

                                        </ul>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </section>
                )
            })}

            <Footer />
        </div>
    );
}

export default NewsSingle;