import React, { useEffect } from "react";
import ColumnZero from "../components/ColumnZero";
import ColumnZeroTwo from "../components/ColumnZeroTwo";
import ColumnZeroThree from "../components/ColumnZeroThree";
import Footer from "../components/footer";
import { createGlobalStyle } from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";
import "../../GlobalVariables";
import { useState } from "react";
import "../../assets/CSSfiles/ColumnZeroTwo.scss";

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #212428;
  }
`;

const Colection = function () {
  const wallet = useSelector((state) => state.wallet);
  let user = useSelector((state) => state.user);
  const [isSpecificUser, setIsSpecificUser] = useState(false);
  const [userSpecific, setUserSpecific] = React.useState([]);
  useEffect(() => {
    console.log(user);
    if (
      window.location.href == `${global.FrontendUrl}/Author#dreamInvestment` ||
      window.location.href ==
        "http://idealexchange.unialsolutions.com/Author#dreamInvestment"
    ) {
      setOpenMenu1(!openMenu1);
      setOpenMenu2(false);
      setOpenMenu(false);
      document.getElementById("Mainbtn1").classList.add("authorActiveTab");
      document.getElementById("Mainbtn").classList.remove("authorActiveTab");
      document.getElementById("Mainbtn2").classList.remove("authorActiveTab");
    } else if (
      window.location.href == `${global.FrontendUrl}/Author#sharedDreams` ||
      window.location.href ==
        "http://idealexchange.unialsolutions.com/Author#sharedDreams"
    ) {
      setOpenMenu2(!openMenu2);
      setOpenMenu(false);
      setOpenMenu1(false);
      document.getElementById("Mainbtn2").classList.add("authorActiveTab");
      document.getElementById("Mainbtn").classList.remove("authorActiveTab");
      document.getElementById("Mainbtn1").classList.remove("authorActiveTab");
    } else {
      console.log("route not found");
    }
    if (window.location.href.includes("Author/user/")) {
      console.log(window.location);
      let splits = window.location.pathname.split("/");
      console.log(splits);
      let longId = splits[3];
      console.log(longId);
      let id = longId.slice(5, longId.length);
      console.log(id);
      const formData = {
        user_id: id,
      };
      axios
        .post(
          `${global.Laravel_API_URL}/api/mint-tokens/get-user-by-id`,
          formData
        )
        .then((responseUser) => {
          console.log(responseUser);
          let values = {
            userData: [responseUser.data.users],
          };
          console.log(values);
          setUserSpecific(values);
          // user = values
          // console.log(user)
          setIsSpecificUser(true);
        });
    } else {
      setIsSpecificUser(true);
      setUserSpecific(user);
    }
  }, []);

  const [openMenu, setOpenMenu] = React.useState(true);
  const [openMenu1, setOpenMenu1] = React.useState(false);
  const [openMenu2, setOpenMenu2] = React.useState(false);
  const handleBtnClick = (): void => {
    setOpenMenu(!openMenu);
    setOpenMenu1(false);
    setOpenMenu2(false);
    document.getElementById("Mainbtn").classList.add("authorActiveTab");
    document.getElementById("Mainbtn1").classList.remove("authorActiveTab");
    document.getElementById("Mainbtn2").classList.remove("authorActiveTab");
  };
  const handleBtnClick1 = (): void => {
    setOpenMenu1(!openMenu1);
    setOpenMenu2(false);
    setOpenMenu(false);
    document.getElementById("Mainbtn1").classList.add("authorActiveTab");
    document.getElementById("Mainbtn").classList.remove("authorActiveTab");
    document.getElementById("Mainbtn2").classList.remove("authorActiveTab");
  };
  const handleBtnClick2 = (): void => {
    setOpenMenu2(!openMenu2);
    setOpenMenu(false);
    setOpenMenu1(false);
    document.getElementById("Mainbtn2").classList.add("authorActiveTab");
    document.getElementById("Mainbtn").classList.remove("authorActiveTab");
    document.getElementById("Mainbtn1").classList.remove("authorActiveTab");
  };

  return (
    <div>
      <GlobalStyles />

      <section className="container no-bottom">
        <div className="row">
          <div className="spacer-double"></div>
          <div className="col-md-12">
            <div className="d_profile de-flex">
              <div className="de-flex-col">
                <div className="profile_avatar">
                  {/* {isSpecificUser == false ?
                    <img src={user.userData.length > 0 ? `${global.BackendUrl}/nodeassets/${user.userData[0].user_image}` : ""} alt="userImage" />
                    : */}
                  <img
                    src={
                      isSpecificUser == true
                        ? userSpecific.userData.length > 0
                          ? `${global.BackendUrl}/nodeassets/${userSpecific.userData[0].user_image}`
                          : ""
                        : ""
                    }
                    alt="userImage"
                  />
                  {/* } */}
                  <div className="profile_name">
                    {/* {isSpecificUser == false ?
                      <h4>
                        {user.userData.length > 0 ? user.userData[0].user_name : ""}
                        <span className="profile_username">{user.userData.length > 0 ? user.userData[0].user_email : ""}</span>
                        <span id="wallet" className="profile_wallet">{user.userData.length > 0 ? user.userData[0].user_address : wallet.address}</span>
                        <button onClick={() => {
                          navigator.clipboard.writeText(user.userData.length > 0 ? user.userData[0].user_address : wallet.address)
                        }} id="btn_copy" title="Copy Text">Copy</button>
                      </h4>
                      : */}
                    <h4>
                      {isSpecificUser == true
                        ? userSpecific.userData.length > 0
                          ? userSpecific.userData[0].user_name
                          : ""
                        : ""}
                      <span className="profile_username">
                        {isSpecificUser == true
                          ? userSpecific.userData.length > 0
                            ? userSpecific.userData[0].user_email
                            : ""
                          : ""}
                      </span>
                      <span id="wallet" className="profile_wallet">
                        {isSpecificUser == true
                          ? userSpecific.userData.length > 0
                            ? userSpecific.userData[0].user_address
                            : wallet.address
                          : ""}
                      </span>
                      <button
                        onClick={() => {
                          navigator.clipboard.writeText(
                            isSpecificUser == true
                              ? userSpecific.userData.length > 0
                                ? userSpecific.userData[0].user_address
                                : wallet.address
                              : ""
                          );
                        }}
                        id="btn_copy"
                        title="Copy Text"
                      >
                        Copy
                      </button>
                    </h4>
                    {/* } */}
                  </div>
                </div>
              </div>
              <div className="profile_follow de-flex">
                <div className="de-flex-col">
                  <span
                    onClick={() => window.open("/profile", "_self")}
                    className="btn-main lead"
                    style={{ borderRadius: "6px" }}
                  >
                    Update Profile
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="container no-top">
        <h4 style={{ marginBottom: "20px" }}>
          <span id="wallet" class="profile_wallet">
            Your deposit :
          </span>
          <span style={{ marginLeft: "20px" }}>0 ETH</span>
        </h4>
        <div className="row">
          <div className="col-lg-12">
            <div className="items_filter">
              <ul className="de_nav text-left">
                <li id="Mainbtn" className="authorActiveTab">
                  <span onClick={handleBtnClick}>IP</span>
                </li>
                <li id="Mainbtn1" className="">
                  <span onClick={handleBtnClick1}>Dream Investments</span>
                </li>
                <li id="Mainbtn2" className="">
                  <span onClick={handleBtnClick2}>My Shared Dreams</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
        {openMenu && (
          <div id="zero1" className="onStep fadeIn row ColumnZeroTwo">
            {isSpecificUser == true ? (
              <ColumnZero user={userSpecific} currentUser={user} />
            ) : null}
          </div>
        )}
        {openMenu1 && (
          <div id="zero2" className="onStep fadeIn row ColumnZeroTwo">
            {/* <p>No Dream Investments</p> */}
            {console.log("currentuser", user)}

            {isSpecificUser == true ? (
              <ColumnZeroTwo user={userSpecific} currentUser={user} />
            ) : null}
          </div>
        )}
        {openMenu2 && (
          <div id="zero3" className="onStep fadeIn">
            <p>No Shared Dreams</p>
            {/* <ColumnZeroThree /> */}
          </div>
        )}
      </section>

      <Footer />
    </div>
  );
};
export default Colection;
