const Explore = () => {
  const user = useSelector((state) => state.user);
  const [mintList, setMintList] = useState([]);
  const [AllData, setAllData] = useState(data);
  const [NewData, setNewData] = useState(data);
  useEffect(async () => {
    // let walletAddress = window.localStorage.getItem("walletAddress");
    // console.log(walletAddress)
    // const formData = {
    //   minted_tokens_wallet_address: walletAddress
    // }
    // axios.post(`${global.Laravel_API_URL}/api/mint-tokens/get-mint-list`, formData).then(response => {
    //   console.log(response);
    //   setMintList(response.data.minted_tokens)
    // })
    getTokens();
  }, []);
  const [filter, setfilter] = useState("All");
  const [SearchField, setSearchField] = useState("");

  function getTokens() {
    axios
      .get(`${global.Laravel_API_URL}/api/mint-tokens/get-mint-list-all`)
      .then((response) => {
        console.log(response);
        let mintedTokensList = response.data.minted_tokens;
        let userNFTList = response.data.nft_user;
        let usersList = response.data.users;
        let nftLikes = response.data.tbl_nft_likes;
        let nftsArray = [];
        mintedTokensList.forEach((element) => {
          console.log(element);
          const filteredUserNFT = userNFTList.filter(
            (e) => e.nftitem_id === element.nftitem_id
          );
          console.log(filteredUserNFT);
          if (filteredUserNFT.length > 0) {
            const filteredusers = usersList.filter(
              (e) => e.user_id === filteredUserNFT[0].user_nft_id
            );
            console.log(filteredusers);
            if (filteredusers.length > 0) {
              const filteredLikes = nftLikes.filter(
                (e) => e.nftitem_id === element.nftitem_id
              );
              let isCurrentUserLiked = [];
              let liked = false;
              if (user.userData.length > 0) {
                isCurrentUserLiked = nftLikes.filter(
                  (e) =>
                    e.user_nft_id == user.userData[0].user_id &&
                    e.nftitem_id == element.nftitem_id
                );
                console.log(isCurrentUserLiked);
              }
              if (isCurrentUserLiked.length > 0) {
                liked = true;
              }

              let likes = filteredLikes.length;

              nftsArray.push({
                nftitem_id: element.nftitem_id,
                nftitem_address: element.nftitem_address,
                nftitem_media: element.nftitem_media,
                nftitem_category: element.nftitem_category,
                nftitem_title: element.nftitem_title,
                nftitem_description: element.nftitem_description,
                nftitem_shares_total: element.nftitem_shares_total,
                nftitem_estimated_eth: element.nftitem_estimated_eth,
                nftitem_external_link: element.nftitem_external_link,
                nftitem_properties: element.nftitem_properties,
                nftitem_extrafiles: element.nftitem_extrafiles,
                nftitem_status: element.nftitem_status,
                user_nft_id: filteredUserNFT[0].user_nft_id,
                nftitem_id: filteredUserNFT[0].nftitem_id,
                nftitem_royalities: filteredUserNFT[0].nftitem_royalities,
                nft_item_timestamp: filteredUserNFT[0].nft_item_timestamp,
                user_name: filteredusers[0].user_name,
                user_email: filteredusers[0].user_email,
                user_address: filteredusers[0].user_address,
                user_bio: filteredusers[0].user_bio,
                user_image: filteredusers[0].user_image,
                user_status: filteredusers[0].user_status,
                user_id: filteredusers[0].user_id,
                likes: likes,
                liked: liked,
              });
            }
          }
        });
        console.log("nftsArray", nftsArray);
        setAllData(nftsArray);
        setNewData(nftsArray);
        // this.setState({
        //   nfts: nftsArray
        // })
      });
  }
  function makeid(length) {
    var result = "";
    var characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }

  const handlechange = (e) => {
    if (e == null) {
      setfilter((filter) => e);
    } else {
      setfilter((filter) => e.value);
      console.log("filter", filter);
    }
  };

  useEffect(() => {
    axios
      .get(`${global.BackendUrl}/getHeroBg`)
      .then((response) => {
        setHeroBg(response.data);
      })
      .catch((err) => {
        console.log(err);
      });

    if (filter == "All") {
      setNewData(AllData);
    } else {
      setNewData(
        AllData.filter((item) => {
          return item.Catergory == filter;
        })
      );
    }
  }, [filter]);

  const filterOptions = (
    candidate: { label: string, value: string, data: any },
    input: string
  ) => {
    if (input) {
      return candidate.value === options[0].value;
    }
    return true;
  };

  const [HeroBg, setHeroBg] = useState([]);

  return (
    <div>
      <section
        className="jumbotron breadcumb no-bg"
        style={{
          backgroundImage: `url(https://idxadminpanel.unialsolutions.com/assets/front/img/sliders/${HeroBg.map(
            (img) => {
              return img.image;
            }
          )})`,
        }}
      >
        <div className="mainbreadcumb">
          <div className="container">
            <div className="row m-10-hor">
              <div className="col-12">
                <h1 className="text-center">Invest</h1>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="container">
        <div className="row">
          <div className="col-lg-12">
            <div className="items_filter">
              <form
                className="row form-dark"
                id="form_quick_search"
                name="form_quick_search"
              >
                <div className="col">
                  <input
                    className="form-control"
                    id="name_1"
                    name="name_1"
                    placeholder="search item here..."
                    type="text"
                    onChange={(e) => setSearchField(e.target.value)}
                  />{" "}
                  <button id="btn-submit">
                    <i className="fa fa-search bg-color-secondary"></i>
                  </button>
                  <div className="clearfix"></div>
                </div>
              </form>
              <div className="dropdownSelect one">
                <Select
                  className="select1"
                  styles={customStyles}
                  menuContainerStyle={{ zIndex: 999 }}
                  options={options}
                  backgroundColor="white"
                  isCleared
                  filterOption={filterOptions}
                  onChange={(e) => {
                    handlechange(e);
                  }}
                />
              </div>
            </div>
          </div>
        </div>
        <div className="Invest_carousel row">
          {NewData.length > 0
            ? NewData.filter((post) => {
                if (SearchField === "") {
                  return post;
                } else if (
                  post.nftitem_title
                    .toLowerCase()
                    .includes(SearchField.toLowerCase())
                ) {
                  return post;
                }
              }).map((item, index) => {
                return <ColumnNew data={item} key={index} user={user} />;
              })
            : "Nothing found"}
        </div>
      </section>
      <Footer />
    </div>
  );
};
export default Explore;
