import React, { useState } from 'react'
import {
    FacebookShareButton,
    FacebookIcon,
    LinkedinShareButton,
    LinkedinIcon,
    TwitterShareButton,
    TwitterIcon
} from "react-share";
import "../../GlobalVariables"
import "../../assets/CSSfiles/News.css"
import { Link } from '@reach/router';

const NavLink = props => (
    <Link
        {...props}
        getProps={({ isCurrent }) => {
            // the object returned here is passed to the
            // anchor element's props
            return {
                className: isCurrent ? 'active' : 'non-active',
            };
        }}
    />
);

const Blog = ({ title, image, id }) => {

    const [ShareBtn, setShareBtn] = useState(false)

    return (
        <div className="col-lg-4 col-md-6 mb30">
            <div className="bloglist item">
                <div className="post-content">
                    <div className="post-image">
                        <img alt="" src={`https://idxadminpanel.unialsolutions.com/assets/front/img/blogs/${image}`} className="lazy" />
                    </div>
                    <div className="post-text">
                        <span className="p-tagline">Tips &amp; Tricks</span>
                        <span className="p-date">October 28, 2020</span>
                        <h4><span>{title}<span></span></span></h4>
                        <p>Dolore officia sint incididunt non excepteur ea mollit commodo ut enim reprehenderit cupidatat labore ad laborum consectetur consequat...</p>
                        <div className='d-flex justify-content-between align-items-center' style={{ position: "relative" }}>
                            <span className="btn-main"><NavLink to={`/newsSingleGrey/${id}`} style={{ textDecoration: "none", color: "white" }}>ReadMore</NavLink></span>
                            <i className="fa fa-share BlogShareIcon" onClick={() => setShareBtn(!ShareBtn)}></i>
                            <div className={`BlogSocialIconsBox ${ShareBtn ? "d-flex" : "hideSocialShare"}  flex-column justify-content-around`} style={{ position: "absolute", bottom: "80%", right: 0 }}>
                                <FacebookShareButton url={`${global.FrontendUrl}/newsSingleGrey/${id}`}>
                                    <FacebookIcon size={30} round={true} />
                                </FacebookShareButton>
                                <LinkedinShareButton url={`${global.FrontendUrl}/newsSingleGrey/${id}`}>
                                    <LinkedinIcon size={30} round={true} />
                                </LinkedinShareButton>
                                <TwitterShareButton url={`${global.FrontendUrl}/newsSingleGrey/${id}`}>
                                    <TwitterIcon size={30} round={true} />
                                </TwitterShareButton>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div >
    )
}

export default Blog