import React, { Component } from "react";
import styled from "styled-components";
import Clock from "./Clock";
import axios from "axios";
import { Link } from "@reach/router";

const Outer = styled.div`
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 8px;
`;

const NavLink = (props) => (
  <Link
    {...props}
    getProps={({ isCurrent }) => {
      return {
        className: isCurrent ? "active" : "non-active",
      };
    }}
  />
);

export default class Responsive extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nfts: [],
      height: 0,
    };
    this.onImgLoad = this.onImgLoad.bind(this);
    this.getTokens();
  }
  getTokens = () => {
    console.log(this.props.user);
    if (this.props.user.userData.length > 0) {
      let walletAddress = this.props.user.userData[0].user_address;
      console.log(walletAddress);
      axios
        .get(`${global.Laravel_API_URL}/api/mint-tokens/get-mint-list-all`)
        .then((response) => {
          console.log(response);
          let mintedTokensList = response.data.minted_tokens;
          let userNFTList = response.data.nft_user;
          let usersList = response.data.users;
          let nftLikes = response.data.tbl_nft_likes;
          let nftsArray = [];
          mintedTokensList.forEach((element) => {
            if (element.nftitem_address == walletAddress) {
              console.log(element);
              const filteredUserNFT = userNFTList.filter(
                (e) => e.nftitem_id === element.nftitem_id
              );
              console.log(filteredUserNFT);
              if (filteredUserNFT.length > 0) {
                const filteredusers = usersList.filter(
                  (e) => e.user_id === filteredUserNFT[0].user_nft_id
                );
                console.log(filteredusers);
                if (filteredusers.length > 0) {
                  const filteredLikes = nftLikes.filter(
                    (e) => e.nftitem_id === element.nftitem_id
                  );
                  let isCurrentUserLiked = [];
                  let liked = false;
                  if (this.props.currentUser.userData.length > 0) {
                    isCurrentUserLiked = nftLikes.filter(
                      (e) =>
                        e.user_nft_id ==
                          this.props.currentUser.userData[0].user_id &&
                        e.nftitem_id == element.nftitem_id
                    );
                    console.log(isCurrentUserLiked);
                  }
                  if (isCurrentUserLiked.length > 0) {
                    liked = true;
                  }

                  let likes = filteredLikes.length;

                  nftsArray.push({
                    nftitem_id: element.nftitem_id,
                    nftitem_address: element.nftitem_address,
                    nftitem_media: element.nftitem_media,
                    nftitem_category: element.nftitem_category,
                    nftitem_title: element.nftitem_title,
                    nftitem_description: element.nftitem_description,
                    nftitem_shares_total: element.nftitem_shares_total,
                    nftitem_estimated_eth: element.nftitem_estimated_eth,
                    nftitem_external_link: element.nftitem_external_link,
                    nftitem_properties: element.nftitem_properties,
                    nftitem_extrafiles: element.nftitem_extrafiles,
                    nftitem_status: element.nftitem_status,
                    user_nft_id: filteredUserNFT[0].user_nft_id,
                    nftitem_id: filteredUserNFT[0].nftitem_id,
                    nftitem_royalities: filteredUserNFT[0].nftitem_royalities,
                    nft_item_timestamp: filteredUserNFT[0].nft_item_timestamp,
                    user_name: filteredusers[0].user_name,
                    user_email: filteredusers[0].user_email,
                    user_address: filteredusers[0].user_address,
                    user_bio: filteredusers[0].user_bio,
                    user_image: filteredusers[0].user_image,
                    user_status: filteredusers[0].user_status,
                    user_id: filteredusers[0].user_id,
                    likes: likes,
                    liked: liked,
                  });
                }
              }
            }
          });
          console.log("nftsArray", nftsArray);
          this.setState({
            nfts: nftsArray,
          });
        });
    }
  };
  onImgLoad({ target: img }) {
    let currentHeight = this.state.height;
    if (currentHeight < img.offsetHeight) {
      this.setState({
        height: img.offsetHeight,
      });
    }
  }
  makeid(length) {
    var result = "";
    var characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }
  likeNFT = (nftitem_id, user_nft_id) => {
    let walletAddress = window.localStorage.getItem("walletAddress");
    console.log(walletAddress);
    if (
      walletAddress != undefined &&
      walletAddress != null &&
      walletAddress != ""
    ) {
      console.log(nftitem_id);
      console.log(user_nft_id);
      // if(liked==false)
      // {
      const formData = {
        user_nft_id: user_nft_id,
        nftitem_id: nftitem_id,
      };
      axios
        .post(
          `${global.Laravel_API_URL}/api/mint-tokens/like-dislike-nft`,
          formData
        )
        .then((response) => {
          console.log(response);
          if (response.data.status == 200) {
            this.getTokens();
          }
        });
      // }
    } else {
    }
  };

  render() {
    return (
      <>
        {this.state.nfts.map((nft, index) => (
          <div className="col-12 col-sm-6 col-lg-3 col-md-4 col-xl-3 col-xxl-3 d-flex justify-content-center my-2">
            <div key={index} className="carousel_item">
              <div className="author">
                <span
                  style={{ position: "relative" }}
                  onClick={() => window.open(nft.authorLink, "_self")}
                >
                  <img
                    className="author_img"
                    src={
                      this.props.user.userData.length > 0
                        ? `${global.BackendUrl}/nodeassets/${this.props.user.userData[0].user_image}`
                        : ""
                    }
                    alt=""
                  />
                  <i
                    className="fa fa-check"
                    style={{ position: "absolute", top: "20px", right: "7px" }}
                  ></i>
                </span>
              </div>
              <div
                className="nft_img_wrap"
                // style={{
                //   height: `${this.state.height}px`,
                //   marginTop: "60px",
                //   minHeight: "200px",
                // }}
              >
                <Outer>
                  <img
                    onLoad={this.onImgLoad}
                    src={global.BackendUrl + "/nodeassets/" + nft.nftitem_media}
                    className="lazy nft_img"
                    alt=""
                  />
                </Outer>
              </div>
              <div className="nft_item_info">
                <NavLink to={"/ItemDetail/" + this.makeid(5) + nft.nftitem_id}>
                  <span>
                    <h4 className="nft_heading">{nft.nftitem_title}</h4>
                  </span>
                </NavLink>
                <div>
                  {nft.nftitem_shares_total} /{nft.nftitem_royalities}
                </div>
                <div className="idx_like">
                  <div
                    style={{ color: "white" }}
                    onClick={() => window.open(nft.bidLink, "_self")}
                  >
                    Place a bid
                  </div>
                  <div
                    onClick={(e) => {
                      if (this.props.currentUser.userData.length > 0) {
                        this.likeNFT(
                          nft.nftitem_id,
                          this.props.currentUser.userData[0].user_id
                        );
                      }
                    }}
                    className={`nft__item_like ${
                      nft.liked == true ? "nft__item_like_active" : ""
                    }`}
                  >
                    <i className="fa fa-heart"></i>
                    <span>{nft.likes}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </>
    );
  }
}
