import React, { useState, useEffect } from "react";
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import "../../GlobalVariables"
import axios from "axios"
import { useSelector, useDispatch } from 'react-redux'
import { setUserData } from "../../store/UserSlice";
import { navigate } from "@reach/router";


const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #fff;
  }
  .mainside{
    .connect-wal{
      display: none;
    }
    .logout{
      display: flex;
      align-items: center;
    }
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #111;
    }
    .item-dropdown .dropdown a{
      color: #111 !important;
    }
  }
`;

const Profile = () => {
    const dispatch = useDispatch();

    const user = useSelector((state) => state.user)
    const wallet = useSelector((state) => state.wallet);

    const [user_name, setuser_name] = useState("")
    const [user_email, setuser_email] = useState("");
    const [user_bio, setuser_bio] = useState("")
    const [user_status, setuser_status] = useState("1");
    const [user_image, setuser_image] = useState("");
    const [HeroBg, setHeroBg] = useState([])
    useEffect(() => {
        axios.get(`${global.BackendUrl}/getHeroBg`).then((response) => {
            setHeroBg(response.data)
        }).catch((err) => {
            console.log(err)
        })
    }, [])
    const UpdateUser = async () => {

        const data = new FormData();
        data.append("userId", user.userData.length > 0 ? user.userData[0].user_id : "")
        data.append("user_name", user_name)
        data.append("user_email", user_email)
        data.append("user_address", wallet.address)
        data.append("user_image", user_image)
        data.append("user_bio", user_bio)
        data.append("user_status", user_status)

        await axios.put(`${global.BackendUrl}/updateUser`, data).then((response) => {
            console.log(response)
        }).catch((err) => {
            console.log(err)
        })

        await axios.post(`${global.BackendUrl}/getUser`, {
            userId: user.userData.length > 0 ? user.userData[0].user_address : ""
        }).then((response) => {
            console.log("Updated Data", response.data)
            dispatch(setUserData(response.data))
            navigate("/Author")
        }).catch((err) => {
            console.log(err)
        })
    }


    return (
        <div>
            <GlobalStyles />
            <section id='profile_banner' className='jumbotron breadcumb no-bg' style={{ backgroundImage: `url(https://idxadminpanel.unialsolutions.com/assets/front/img/sliders/${HeroBg.map((img) => { return img.image })})` }}>
                <div className='mainbreadcumb'>
                    <div className='container'>
                        <div className='row m-10-hor'>
                            <div className='col-12'>
                                <h1 className='text-center' style={{ fontWeight: "700", fontSize: "50px" }}>Profile Settings</h1>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section id="section-main" aria-label="section">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-10 offset-lg-1 d-flex">
                            <form className="form-border w-100" onSubmit={(e) => { e.preventDefault() }} encType="multipart/form-data">
                                <div className="de_tab tab_simple">
                                    <div className="de_tab_content">
                                        <div className="tab-1">
                                            <div className="row wow fadeIn animated" style={{ backgroundSize: 'cover', visibility: 'visible', animationName: 'fadeIn' }}>
                                                <div className="col-lg-8 mb-sm-20">
                                                    <div className="field-set">
                                                        <h5>Username</h5>
                                                        <input type="text" name="user_name" id="username" className="form-control" placeholder="Enter username" onChange={(e) => { setuser_name(e.target.value) }} />
                                                        <div className="spacer-20"></div>
                                                        <h5>Bio</h5>
                                                        <input component="textarea" name="user_bio" id="about" className="form-control" placeholder="Tell the world who you are!" onChange={(e) => { setuser_bio(e.target.value) }} />

                                                        <div className="spacer-20"></div>

                                                        <h5>Email Address*</h5>
                                                        <input type="email" name="user_email" id="social" className="form-control" placeholder="Enter email" onChange={(e) => { setuser_email(e.target.value) }} />
                                                        <div className="spacer-20"></div>
                                                    </div>
                                                </div>
                                                <div id="sidebar" className="col-lg-4">
                                                    <h5>Profile image <i className="fa fa-info-circle id-color-2" data-bs-toggle="tooltip" data-bs-placement="top" title="" data-bs-original-title="Recommend 400 x 400. Max size: 50MB. Click the image to upload." aria-label="Recommend 400 x 400. Max size: 50MB. Click the image to upload."></i></h5>
                                                    <img
                                                        src={user.userData.length > 0 ? `${global.BackendUrl}/nodeassets/${user.userData[0].user_image}` : ""}
                                                        id="click_profile_img"
                                                        className="d-profile-img-edit img-fluid"
                                                        alt=""
                                                        style={{ width: '150px', height: '150px', objectFit: 'cover', borderRadius: "50%" }}
                                                    />
                                                    <input name="user_image" type="file" id="upload_profile_img" className="mt-2" onChange={(e) => setuser_image(e.target.files[0])} />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <input type="submit" id="submit" className="btn-main" value="Update profile" onClick={UpdateUser} />
                            </form>



                            {/* different form for image and banner */}
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default Profile;