import React, { useState } from 'react';
import { ethers } from 'ethers';
import { useDispatch } from "react-redux"
import { setAddress, setWalletStatus } from '../../store/WalletSlice';
import { useNavigate } from "@reach/router"
import axios from "axios"
import { setUserData } from '../../store/UserSlice';

const Wallet = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const connectMetamaskHandler = () => {
        if (window.ethereum && window.ethereum.isMetaMask) {
            console.log('MetaMask Here!');

            window.ethereum.request({ method: 'eth_requestAccounts' })
                .then(result => {
                    console.log('Wallet Connected');
                    accountChangedHandler(result[0]);
                    window.localStorage.setItem("walletAddress", result[0]);
                    dispatch(setAddress(result[0]))
                    dispatch(setWalletStatus(1))
                    getAccountBalance(result[0]);

                    storeUser(result[0])
                })
                .catch(error => {
                    console.log(error)
                });

        } else {
            console.log('Need to install MetaMask');
        }
    }

    const storeUser = async (address) => {
        await axios.post(`${global.BackendUrl}/createUser`, {
            user_address: address
        }).then((response) => {
            console.log("Create Response", response)
        }).catch((err) => {
            console.log(err)
        })

        await axios.post(`${global.BackendUrl}/getUser`, {
            userId: address
        }).then((response) => {
            console.log(response)
            dispatch(setUserData(response.data))
            console.log(window.location.href)
            if (window.location.href == `${global.FrontendUrl}/wallet#create` || window.location.href == `http://idealexchange.unialsolutions.com/wallet#create`) {
                navigate("/create2")
            }
            else {
                navigate("/home1")
            }
        }).catch((err) => {
            console.log(err)
        })
    }

    // update account, will cause component re-render
    const accountChangedHandler = (newAccount) => {
        getAccountBalance(newAccount.toString());
    }

    const getAccountBalance = (account) => {
        window.ethereum.request({ method: 'eth_getBalance', params: [account, 'latest'] })
            .then(balance => {
                console.log(ethers.utils.formatEther(balance));
            })
            .catch(error => {
                console.log(error.message);
            });
    };

    const chainChangedHandler = () => {
        // reload the page to avoid any errors with chain change mid use of application
        window.location.reload();
    }


    // listen for account changes
    // window.ethereum.on('accountsChanged', accountChangedHandler);

    // window.ethereum.on('chainChanged', chainChangedHandler);

    return (
        <>
            <div className="row d-flex justify-content-center align-items-center" >
                <div className="col-lg-3 mb30 mx-1" onClick={connectMetamaskHandler}>
                    <span className="box-url">
                        <span className="box-url-label">Most Popular</span>
                        <img src="./img/wallet/1.png" alt="" className="mb20" />
                        <h4 style={{ letterSpacing: "3px" }}>Metamask</h4>
                        <p>Start exploring blockchain applications in seconds.  Trusted by over 1 million users worldwide.</p>
                    </span>
                </div>
                <div className="col-lg-3 mb30 mx-1">
                    <span className="box-url">
                        {/* <span className="box-url-label">Most Popular</span> */}
                        <img height="70" src="./img/wallet/paypal.png" alt="" className="mb20" />
                        <h4 style={{ letterSpacing: "3px" }}>Paypal</h4>
                        <p>Start exploring online payment transfers in seconds. Trusted by over 496 million users worldwide.</p>
                    </span>
                </div>
            </div>
        </>
    )
};
export default Wallet;