import React, { useEffect, useState } from 'react';
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import "../../GlobalVariables"
import axios from 'axios';
import Blog from '../components/Blog';

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #212428;
  }
`;

const News = () => {

  useEffect(() => {
    axios.get(`${global.BackendUrl}/getBlogs`).then((response) => {
      setBlogsData(response.data)
    }).catch((err) => {
      console.log(err)
    })
    axios.get(`${global.BackendUrl}/getHeroBg`).then((response) => {
      setHeroBg(response.data)
    }).catch((err) => {
      console.log(err)
    })
  }, [])
  const [HeroBg, setHeroBg] = useState([])
  const [BlogsData, setBlogsData] = useState([])

  return (
    <div>
      <GlobalStyles />
      <section className='jumbotron breadcumb no-bg' style={{ backgroundImage: `url(https://idxadminpanel.unialsolutions.com/assets/front/img/sliders/${HeroBg.map((img) => { return img.image })})` }}>
        <div className='mainbreadcumb'>
          <div className='container'>
            <div className='row m-10-hor'>
              <div className='col-12'>
                <h1 className='text-center'>Blogs</h1>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className='container'>
        <div className="row">
          {BlogsData.length > 0 ? BlogsData.map((data) => {
            return (
              <Blog key={data.id} title={data.title} image={data.main_image} id={data.id} />
            )
          }) : <p>No Documentation yet</p>}


          <div className="spacer-single"></div>

          <ul className="pagination">
            <li><span className='a'>Prev</span></li>
            <li className="active"><span className='a'>1</span></li>
            <li><span className='a'>2</span></li>
            <li><span className='a'>3</span></li>
            <li><span className='a'>4</span></li>
            <li><span className='a'>5</span></li>
            <li><span className='a'>Next</span></li>
          </ul>

        </div>
      </section>

      <Footer />
    </div>
  )
};
export default News;